@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;700&display=swap);
body {
    overflow-y: scroll;
    margin: 0;
    font-family: 'Nunito', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 14px;
    color: white;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

* {
    box-sizing: border-box;
}

@media screen and (min-width: 48em) {
    body {
        font-size: 16px;
    }
}
.App {
  text-align: center;
}

.simulator-wrapper {
    padding: 3em 0;
    position: relative;
    width: 100%;
}

.simulator-graph {
    width: 40em;
    max-width: 100%;
    background: white;
    border-radius: 0.2em;
    color: grey;
    padding: 3em;
    margin: 0 auto;
}

.simulator-graph__title {
    color: rgb(102, 168, 255);
    margin: 0;
}

.simulator-invitation {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 1;
}

.simulator-invitation__text {
    text-align: center;
    font-size: 1.5em;
    font-weight: bold;
    width: 20em;
    max-width: 100%;
    display: inline-block;
}

.simulator-invitation__button {
    text-transform: uppercase;
    font-weight: bold;
    color: white;
    display: inline-block;
    padding: 0.5em 1em;
    cursor: pointer;
    border-radius: 0.2em;
    border: 2px solid transparent;
    text-decoration: none;
    background: rgb(52, 103, 235);
    transition: border-color 0.3s ease-in-out;
    font-size: 1em;
    outline: none;
    line-height: 1.3;
}

.simulator-invitation__button:hover,
.simulator-invitation__button:focus {
    border-color: white;
}

.simulator-new-candidate {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 0;
    max-width: 100%;
    width: 30em;
    z-index: 2;
    background: red;
    padding: 2em;
    border-radius: 0.2em;
}

.simulator-new-candidate__title {
    margin: 0;
    font-size: 1.5em;
    font-weight: bold;
    color: white;
}

.simulator-new-candidate__text {
    font-weight: bold;
    color: white;
}

.vote-wrapper {
    overflow: auto;
}

.vote {
    margin-top: 2em;
}

.vote-label {
    font-weight: bold;
    margin-bottom: .5em;
    display: block;
}

.vote-percent {
    display: block;
    width: 100%;
}

    .vote-percent__progress {
        height: 1em;
        background: black;
        border-radius: .5em;
        transition: width .1s linear;
    }
.winner {
    position: absolute;
    left: 50%;
    top: 7em;
    transform: translateX(-50%);
    padding: 2em;
    background: white;
    border-radius: 0.2em;
    border: 3px solid grey;
    opacity: 0;
    transition: opacity 3s ease-out;
    max-width: 100%;
}

.winner--visible {
    opacity: 1;
}

.winner__announcement {
    text-align: center;
    color: grey;
    font-size: 1.3em;
    font-weight: bold;
    text-transform: uppercase;
}

.winner__image {
    max-width: 100%;
    margin: 0 auto;
}

.logo-wrapper {
    margin-bottom: 2em;
}

.logo {
    width: 30em;
    max-width: 100%;
    margin: 0 auto;
    background: white;
    color: grey;
    overflow: auto;
    border-radius: .2em;
}

    .logo__title {
        text-align: center;
        padding: 2em 1em 0;
        margin: 0;
        font-size: 1.3em;
    }

    .logo__item {
        margin: 1em 0 3em;
    }

    .logo__text {
        text-align: center;
        margin: 0;
    }

    .logo__img {
        width: 20em;
        max-width: 90%;
        display: block;
        margin: 0 auto;
    }
.calculator {
    margin: 0 auto;
    background: white;
    border-radius: 0.2em;
    padding: 2em;
    color: grey;
    max-width: 30em;
}

.calculator__reminder {
    color: black;
    font-size: 1.5em;
    font-weight: bold;
    margin-top: 0;
}

.calculator__input-wrapper {
    overflow: hidden;
}

.calculator__data {
    border-top: 1px solid grey;
    padding-top: 2em;
    margin-top: 2em;
}

.calculator__input {
    border-radius: .2em;
    font-size: 1em;
    line-height: 1;
    height: 3em;
    background: white;
    box-sizing: border-box;
    display: block;
    padding: 1em;
    width: 100%;
    border: none;
    box-shadow: 0 0 0 1px inset grey;
    transition: box-shadow .3s ease-in-out;
}

.calculator__input--amount {
    width: calc(100% - 8em);
    float: left;
}

.calculator__input--currency {
    width: 7em;
    float: right;
}

.calculator__input:focus {
    outline: none;
    box-shadow: 0 0 0 2px inset grey;    
}

.calculator__label {
    display: block;
    font-weight: bold;
    margin-bottom: .5em;
}

.calculator__amount {
    color: black;
    margin: 0;
}

.calculator__amount--bold {
    font-weight: bold;
}

.calculator__block {
    margin-bottom: 1em;
    overflow: hidden;
}

.calculator__block:last-child {
    margin-bottom: 0;
}
.container__background {
    position: fixed;
    z-index: -1;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    transition: background-color 0.3s ease-in-out;
}

.container__background--primary {
    background: rgb(102, 168, 255);
}

.container__background--secondary {
    background: rgb(255, 90, 90);
}

.container__background--tertiary {
    background: rgb(40, 180, 120);
}

.menu-wrapper {
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    padding: 1em 2em;
    background: rgba(120, 120, 120, .6);
    justify-content: space-between;
    z-index: 2;
}

.header-logo {
    float: left;
    display: none;
    align-items: center;
}

.header-logo__img {
    border-radius: 50%;
    width: 3.5em;
}

.header-logo__title {
    margin: 0 0 0 1em;
    line-height: 1;
    font-size: 1.5em;
    display: none;
}

@media screen and (min-width: 26em) {
    .header-logo {
        display: flex;
    }
}

@media screen and (min-width: 50em) {
    .header-logo__title {
        display: block;
    }
}

.menu {
    float: right;
    display: flex;
}

    .menu__item {
        font-weight: bold;
        color: white;
        display: inline-block;
        padding: .5em 1em;
        border-radius: .2em;
        border: 2px solid transparent;
        text-decoration: none;
        background: rgba(255, 255, 255, .1);
        transition: border-color .3s ease-in-out;
    }

    .menu__item--active, .menu__item:hover, .menu__item:focus {
        border-color: white;
        outline: none;
    }

    .menu__item + .menu__item {
        margin-left: 1em;
    }
.content {
    padding: 7em 2em 2em;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

